import logo from './logo.svg';
import Todo from './Todo'; // Todo  import
//import Count from './Count';
import React, { useState, useEffect } from 'react';
import { Container, List, Paper, Grid, Button, AppBar, Toolbar, Typography } from "@mui/material";
import './App.css';
import AddTodo from './AddTodo'
import { call, signout } from './service/ApiService';
import { SpaceBar } from '@mui/icons-material';

//
//  React      
function App() {

  //Todo   state
  const [items, setItems] = useState([])

  //  state
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // 
    call("/todo", "GET")// /Todo   .
      .then(result => {
        setItems(result.data);
        setLoading(false);
      })
  }, [])

  //
  //  App.js   
  //App.js 
  const addItem = (item) => {
    call("/todo", "POST", item)
      .then(result => setItems(result.data))
  }

  // 
  const editItem = (item) => {
    call("/todo", "PUT", item)
      .then(result => setItems(result.data))
  }

  //
  const deleteItem = (item) => {
    call("/todo", "DELETE", item)
      .then(result => setItems(result.data))
  }
  //App.js Todo deleteItem 


  //map() ->       .
  //key :    , React    
  //      .
  // key   DOM    
  //KEY   .
  let todoItems = items.length > 0 && (
    <Paper style={{ margin: 16 }}>
      <List>
        {items.map((item) => (
          <Todo item={item} key={item.id} deleteItem={deleteItem} editItem={editItem} />
        ))}
      </List>
    </Paper>
  );
  //JSX :    HTML 
  //Babel : JSX      

  //navigationBar 
  let navigationBar = (
    <AppBar position="static">
      <Toolbar>
        <Grid justifyContent="space-between" container>
          <Grid item>
            <Typography variant='h6'> </Typography>
          </Grid>
          <Grid item>
            {/* variant  : contained  (  )
                                  outlined   
                                  text   */}
            <Button color="inherit" variant="text" onClick={signout}>
              
            </Button>
          </Grid>
        </Grid>
      </Toolbar>
    </AppBar>
  )

  //    
  let todoListPage = (
    <div className="App">
      {navigationBar}
      <Container maxWidth="md">
        <AddTodo addItem={addItem} />
        <div className="TodoList">
          {todoItems}
        </div>
      </Container>
    </div>
  )

  //    
  let loadingPage = <h1>...</h1>
  let content = loadingPage;

  //loading : ture -> 
  //loading : false ->  
  if(!loading){
    //  todoListPage 
    content = todoListPage;
  }


  return (
    <div className="App">
     {content}
    </div>
  );
}

//    .
//export default     ({})  
//  ,        .
export default App;
